import { Component, OnInit } from '@angular/core';
import {StateService} from '../../../service/state.service';
import {DOMAINS} from '../../../mock-data/userInfo';
import {QuestionService} from '../../../service/question.service';

@Component({
  selector: 'app-score-graph',
  templateUrl: './score-graph.component.html',
  styleUrls: ['./score-graph.component.css']
})
export class ScoreGraphComponent implements OnInit {

  userId = JSON.parse(localStorage.getItem('curUser')).id;
  sessionId: number;
  sessions: number[] = [];
  domains = DOMAINS;

  public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels:string[] = [];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = true;
  public scoreData = [];

  public barChartData:any[] = [
    {data: [65, 59, 80, 81, 56, 55, 40], label: 'Your Score'},
    {data: [28, 48, 40, 19, 86, 27, 90], label: 'Average Score'}
  ];

  constructor(
    private stateService: StateService,
    private questionService: QuestionService,
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.stateService.curSession$.subscribe(value => this.sessionId = value);
    });
    this.barChartLabels = this.domains.map(value => {return value.key});
    this.getSession();
    this.getScore();
    this.barChartData = [
      {data: this.scoreData, label: 'Your Score'},
      {data: [28, 48, 40, 19, 86, 27, 90], label: 'Average Score'}
    ];
    console.log(this.scoreData);

  }

  getSession() {
    this.questionService.getSessionByUser(this.userId).subscribe(value => {
      value.forEach( (item) => {
        this.sessions.push(item.id);
      })
      //console.log(value);
    })
  }

  getScore() {
    // this.sessions.forEach( (id) => {

    // })
    const id = this.sessions[0];
    this.questionService.getScoreBySession(id).subscribe( value => {
      this.scoreData = value;
    })
  }
  // events
  public chartClicked(e:any):void {
    console.log(e);
  }

  public chartHovered(e:any):void {
    console.log(e);
  }

}
